html {
	font-size: 18px;
}

body {
	color: #282520;
	background-color: #f0f0f0;
	font-family: "Raleway", sans-serif;
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.55;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

@font-face {
	font-display: swap;
	font-family: "Raleway";
	font-style: normal;
	font-weight: 400;
	src: url("./assets/fonts/raleway-v29-latin-regular.woff2") format("woff2");
}
